// 使用客戶端渲染
'use client';

// 引入必要的React hooks和Next.js組件
import { useState, useEffect } from 'react';
import Image from "next/image";

// 主頁面組件
export default function Home() {
  // 設置功德計數器的state
  const [count, setCount] = useState(0);

  // 添加音效對象的state
  const [audio, setAudio] = useState<HTMLAudioElement | null>(null);
  // 在組件加載時預加載音效
  useEffect(() => {
    const audioElement = new Audio('/muyu.mp3');
    setAudio(audioElement);
  }, []);

  
  // 處理點擊木魚的事件
  const handleKnock = () => {
    if (audio) {
      // 重置音效播放位置並播放
      audio.currentTime = 0;
      audio.play();
    }
    setCount(prev => prev + 1);
  };


  return (
    // 主容器,使用flexbox進行居中布局
    <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-b from-gray-100 to-gray-200">
      {/* 標題 */}
      <h1 className="text-4xl font-bold mb-8">功德木魚</h1>
      
      {/* 木魚點擊區域 */}
      <div className="relative cursor-pointer" onClick={handleKnock}>
        {/* 這裡之後可以放木魚圖片 */}
        
        <div className="w-40 h-40 bg-wood-brown rounded-full flex items-center justify-center text-white">
          點擊敲擊
        </div>
      </div>

      {/* 顯示功德計數 */}
      <div className="mt-8 text-2xl">
        功德數：{count}
      </div>
    </div>
  );
}